import { Slider } from 'preact-range-slider';
import 'preact-range-slider/assets/index.css';

const marks = {
	0: 'Não tenho',
	1: 'Normal',
	2: 'Épico',
	3: 'Lendário'
};

const getPowerLevel = (values, skills) =>
	values.reduce((previousValue, value) => {
		const skill = skills.find(s => s.id === value.skillId);
		const points = skill.levels.reduce(
			(prev, level, idx) => (value.value > idx ? prev + level.value : prev),
			0
		);
		return previousValue + points * skill.weight;
	}, 0);

const getSalary = powerLevel => 450 + 1050 * powerLevel / 10000;

export default ({ values, skills, onRequestChangeValue }) => (
	<div>
		{skills.map((skill, idx) => (
			<div key={idx}>
				<h3>{skill.name}</h3>
				<ul>
					<li>
						<b>Normal:</b> <em>{skill.levels[0].label}</em>
					</li>
					<li>
						<b>Épico:</b> <em>{skill.levels[1].label}</em> +{' '}
						{skill.levels[0].label}
					</li>
					<li>
						<b>Lendário:</b> <em>{skill.levels[2].label}</em> +{' '}
						{skill.levels[1].label} + {skill.levels[0].label}
					</li>
				</ul>
				<div class="px3">
					<Slider
						value={values.find(v => v.skillId === skill.id).value}
						min={0}
						max={3}
						dots
						marks={marks}
						onChange={v =>
							onRequestChangeValue({ skillId: skill.id, value: v })
						}
					/>
				</div>
			</div>
		))}
		{/*<h3>Seu nível de poder: {getPowerLevel(values, skills)}</h3> */}
		<h3 class="center">
			Proposta estimada: R$ {getSalary(getPowerLevel(values, skills))}
		</h3>
	</div>
);



